import { GoogleGenAI, Type } from "@google/genai";

//  Checagem da chave de API
if (!process.env.GEMINI_API_KEY) {
  console.error(" [GeminiService] Varivel de ambiente GEMINI_API_KEY no configurada!");
} else {
  console.log(" [GeminiService] GEMINI_API_KEY detectada com sucesso.");
}

const ai = new GoogleGenAI({ apiKey: process.env.GEMINI_API_KEY as string });

//  URL e chave de autenticao da API remota
const REMOTE_API_URL = process.env.REMOTE_POST_URL || "https://www.iatioben.com.br/api/remote-post";
const REMOTE_API_KEY = process.env.REMOTE_POST_API_KEY;

// ====================================================================
//  1. Criao do artigo jornalstico (texto bruto)
// ====================================================================
export const writeNewsArticle = async (topic: string, language: string, focusKeywords: string): Promise<string> => {
  console.log(" [writeNewsArticle] Iniciando gerao do artigo...");
  console.log(`[writeNewsArticle] Tema: ${topic}`);
  console.log(`[writeNewsArticle] Idioma: ${language}`);
  console.log(`[writeNewsArticle] Palavras-chave: ${focusKeywords}`);

  try {
    const prompt = `
Voc  um jornalista catlico de aproximadamente 25 anos, com um dom especial para contar histrias inspiradoras sobre os Santos da Igreja Catlica. 
Voc escreve para o Blog Tio Ben  um blog catlico jovem e envolvente, que busca evangelizar e inspirar seus leitores atravs da vida dos Santos. 

Seu estilo  leve, natural e empolgante  como um amigo que sabe muito sobre a f e compartilha histrias de forma calorosa e verdadeira. 
No use grias ou expresses populares demais, mas evite tambm a linguagem excessivamente formal. 
Seu texto deve soar autntico, humano e profundamente catlico. 

 **Fontes e Verdade Doutrinal:**
- Suas referncias principais so documentos e fontes oficiais da Igreja Catlica, especialmente o site do Vaticano (vatican.va), o Missal Romano e o Diretrio dos Santos.
- Nunca invente fatos. Se no houver informao sobre algum aspecto, apenas omita. 
- Toda a narrativa deve estar em plena comunho com o Magistrio e a Tradio da Igreja.

 **Estrutura do Artigo (SEO e Contedo):**
O artigo deve ter:
1. **Ttulo** criativo e inspirador.
2. **Subttulo** curto e cativante.
3. **Seo do Dia de Celebrao**  contendo a **Liturgia do Dia** (1 Leitura, 2 Leitura  se houver , Salmo e Evangelho).
4. **Introduo envolvente**, que desperte a curiosidade do leitor e o conecte espiritualmente com o Santo.
5. **Corpo do texto** dividido em sees com subttulos claros:
   -  *Origem e nascimento*: onde e quando nasceu, contexto histrico.
   -  *Caminho de f e misso*: principais fatos da vida, vocao, virtudes.
   -  *Milagres e testemunhos*: fatos reconhecidos pela Igreja ou pela tradio.
   -  *Dia de celebrao*: data litrgica e sentido espiritual.
   -  *Padroeiro e devoes*: se for padroeiro de algo, explique com clareza.
   -  *Orao ao Santo* (caso exista oficialmente na tradio).
   -  *Lio de vida*: o que sua histria ensina para os cristos de hoje, especialmente os jovens.
6. **Concluso inspiradora**, que motive o leitor a buscar uma vida mais santa e prxima de Deus.

 **Recursos estilsticos:**
- Use **emojis** com moderao para dar leveza e simpatia ao texto (ex: , , , , , ).
- Use **listas e tpicos** quando fizer sentido.
- Destaque palavras-chave importantes com **negrito**.
- Os subttulos devem ser marcados com '##' (Markdown) para SEO e clareza visual.
- O texto deve ter uma **densidade equilibrada de palavras-chave** e uma **meta description natural**.

 **SEO e Palavras-chave:**
Durante a escrita, tente incorporar de forma natural e relevante as seguintes palavras-chave: ${focusKeywords}. 
No force a incluso se no parecer natural; priorize a fluidez e o sentido espiritual do texto.

 **Instruo de Escrita:**
Escreva um artigo cativante e detalhado sobre "${topic}". 
O artigo deve ter um ttulo claro, uma introduo envolvente, subttulos temticos, corpo bem estruturado e uma concluso edificante. 
O tom deve ser inspirador e prximo, adequado para jovens catlicos que buscam crescer na f. 
O idioma do artigo deve ser: ${language}.
`;
;

    console.log(" [writeNewsArticle] Enviando prompt para o modelo gemini-2.5-pro...");

    const response = await ai.models.generateContent({
      model: "gemini-2.5-pro",
      contents: [{ parts: [{ text: prompt }] }],
    });

    console.log(" [writeNewsArticle] Artigo gerado com sucesso.");
    return response.text;
  } catch (error) {
    console.error(" [writeNewsArticle] Erro:", error);
    throw new Error("Falha ao gerar o artigo de notcia pela IA.");
  }
};

