// ====================================================================
//  3. Extrao de metadados SEO
// ====================================================================
export const analyzeSeoAndExtractMetadata = async (
  articleText: string,
  focusKeywords: string
): Promise<{ keywords: string[]; metaDescription: string }> => {
  console.log(" [analyzeSeoAndExtractMetadata] Iniciando anlise SEO...");

  try {
    const prompt = `
Voc  um editor catlico do Blog Tio Ben, responsvel por revisar artigos espirituais sobre a vida dos Santos e otimizar seu contedo para SEO  sem jamais perder o tom humano, devocional e inspirador do texto original.

 Sua misso  analisar o artigo abaixo e gerar um **objeto JSON** com duas chaves:
- "keywords": uma lista com 5 a 10 palavras-chave que melhor representam o artigo e sua espiritualidade, valorizando temas catlicos e expresses de f.
- "metaDescription": uma breve descrio (mx. 160 caracteres) que resuma a essncia do artigo de forma cativante, fiel  mensagem e ao estilo do texto, como se fosse o resumo que aparece no Google.  
  Ela deve soar natural, acolhedora e levemente potica  como uma frase que desperta f e curiosidade, sem parecer comercial.

 Dicas de estilo:
- Mantenha o mesmo tom usado no artigo: devocional, alegre, prximo e cheio de esperana.
- Prefira frases que transmitam luz, f, virtude e exemplo de vida.
- No use linguagem tcnica ou publicitria.
- Inspire o leitor a clicar, mas sem perder a reverncia e autenticidade.

Palavras-chave foco do blog: ${focusKeywords}

 Artigo a analisar:
---
${articleText}
---

Retorne **somente o JSON** no formato:
{
  "keywords": ["palavra1", "palavra2", ...],
  "metaDescription": "texto descritivo cativante"
}
`;


    const response = await ai.models.generateContent({
      model: "gemini-2.5-flash",
      contents: [{ parts: [{ text: prompt }] }],
      config: {
        responseMimeType: "application/json",
        responseSchema: {
          type: Type.OBJECT,
          properties: {
            keywords: { type: Type.ARRAY, items: { type: Type.STRING } },
            metaDescription: { type: Type.STRING },
          },
        },
      },
    });

    const result = JSON.parse(response.text);
    console.log(" [analyzeSeoAndExtractMetadata] SEO extrado:", result);
    return {
      keywords: result.keywords || [],
      metaDescription: result.metaDescription || "",
    };
  } catch (error) {
    console.error(" [analyzeSeoAndExtractMetadata] Erro SEO:", error);
    return { keywords: [], metaDescription: "No foi possvel gerar a meta descrio." };
  }
};

// ====================================================================
//  4. Fluxo completo: gerar  formatar  analisar  publicar remotamente
// ====================================================================
export const generateAndPublishArticle = async (
  topic: string,
  language: string,
  focusKeywords: string,
  categoryId: string,
  categoryName = "Santos"
): Promise<void> => {
  console.log(" [generateAndPublishArticle] Iniciando pipeline completo...");
  try {
    // 1 Gera o artigo textual
    const articleText = await writeNewsArticle(topic, language, focusKeywords);

    // 2 Converte para HTML
    const htmlArticle = await formatArticleToHtml(articleText);

    // 3 Extrai SEO
    const { keywords, metaDescription } = await analyzeSeoAndExtractMetadata(articleText, focusKeywords);

    // 4 Prepara os dados para envio remoto
    const slug = topic
      .toLowerCase()
      .normalize("NFD")
      .replace(/[\u0300-\u036f]/g, "")
      .replace(/[^\w\s-]/g, "")
      .replace(/[\s_-]+/g, "-")
      .replace(/^-+|-+$/g, "");

    const DEFAULT_CATEGORY_ID = "ba7adc02-de35-4405-b3f3-7391947d6281";
    const DEFAULT_CATEGORY_NAME = "Santos"; // ou "Notcias", conforme sua estrutura

    const payload = {
      title: topic,
      slug,
      content: htmlArticle,      
      categoryId: DEFAULT_CATEGORY_ID, //  Categoria fixa vlida no Neon
      categoryName: DEFAULT_CATEGORY_NAME,
      keywords: keywords.join(", "),
      metaDescription,
      publishDate: new Date().toISOString(),
      isActive: true,
    };

    console.log(" [generateAndPublishArticle] Payload preparado:", payload);

    if (!REMOTE_API_URL || !REMOTE_API_KEY) {
      console.warn(" [generateAndPublishArticle] Configurao remota ausente, pulando publicao.");
      return;
    }

    // 5 Envia para o outro site
    console.log(` [generateAndPublishArticle] Enviando para ${REMOTE_API_URL} ...`);

    const res = await fetch(REMOTE_API_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-api-key": REMOTE_API_KEY!,
      },
      body: JSON.stringify(payload),
    });

    const result = await res.json();
    if (!res.ok) throw new Error(`Falha na publicao remota: ${result.message}`);

    console.log(" [generateAndPublishArticle] Post publicado com sucesso:", result);
  } catch (error) {
    console.error(" [generateAndPublishArticle] Erro geral:", error);
    throw error;
  }
};
